import React, { useState } from 'react'
import { Modal, Button, Input, Form } from 'antd'

export default function AddApp(props) {
    const { visible, rowData: data = {}, onVisibleChange, onCreate, dispatch } = props

    const [form] = Form.useForm()

    function onConfirm() {
        form.validateFields()
            .then(values => {
                onCreate(values)
            })
            .catch(info => {
                console.log('Validate Failed:', info)
            })
    }

    function onCancel() {
        onVisibleChange()
    }

    return (
        <>
            <Modal title='' visible={visible} onOk={onConfirm} onCancel={onCancel}>
                <Form form={form}>
                    <Form.Item
                        label=''
                        name='appName'
                        rules={[
                            {
                                required: true,
                                message: '!'
                            },
                            {
                                max: 32,
                                message: '!'
                            }
                        ]}>
                        <Input placeholder='32' />
                    </Form.Item>
                    <Form.Item
                        label=''
                        name='appAbbr'
                        rules={[
                            {
                                required: true,
                                message: ''
                            },
                            {
                                max: 32,
                                message: ''
                            }
                        ]}>
                        <Input placeholder='!' />
                    </Form.Item>
                </Form>
            </Modal>
        </>
    )
}
